import React from 'react';
import ImageOnly from '../util/ImageOnly';

import styles from './index.module.scss';

import icon1 from './web1.jpg';
import icon2 from './web2.jpg';
import icon3 from './web3.jpg';
import icon4 from './web4.jpg';

function DesignSubPage(props) {
    return <ImageOnly src={props.src} alt={props.alt} />
}

export default DesignSubPage;

export function Bond() {
    return <DesignSubPage src={icon1} alt="BOND" />
}

export function Bloom() {
    return <DesignSubPage src={icon2} alt="BLOOM" />
}

export function Hole() {
    return <DesignSubPage src={icon3} alt="HOLE" />
}

export function Coco() {
    return <DesignSubPage src={icon4} alt="COCO" />
}
