import React, { Component } from 'react';

import { Route, Switch } from 'react-router-dom';

import Portfolio from './Portfolio';
import PageNotFound from './PageNotFound';
import Timeline from './Timeline';
import Design from './Design';
import {Bond, Bloom, Hole, Coco} from './DesignSubPage';
import Research from './Research';
import Other from './Other';

class App extends Component {
  render() {
    return <React.Fragment>
      <Switch>
        <Route path='/' component={Portfolio} exact={true} />
        <Route path='/timeline' component={Timeline} exact={true} />
        <Route path='/design' component={Design} exact={true} />
        <Route path='/bond' component={Bond} exact={true} />
        <Route path='/bloom' component={Bloom} exact={true} />
        <Route path='/hole' component={Hole} exact={true} />
        <Route path='/coco' component={Coco} exact={true} />
        <Route path='/research' component={Research} exact={true} />
        <Route path='/other' component={Other} exact={true} />
        <Route component={PageNotFound} />
      </Switch>
    </React.Fragment>;
  }
}

export default App;
